"use client";
import { useEffect, useState } from "react";
import dynamic from "next/dynamic";
import Image from "next/image";
import sanityClient from "../sanityClient"; // adjust path
import imageUrlBuilder from "@sanity/image-url";
import styles from "../Style/Brands.module.scss";

// Dynamically import react-slick for Next.js
const Slider = dynamic(() => import("react-slick"), { ssr: false });

// Sanity image URL builder
const builder = imageUrlBuilder(sanityClient);
const urlFor = (source) => (source ? builder.image(source).url() : "");

const PartnersSection = () => {
  const [brands, setBrands] = useState([]);

  useEffect(() => {
    sanityClient
      .fetch(
        `*[_type == "brandimg"] | order(_createdAt asc) {
          name,
          url,
          image
        }`
      )
      .then((data) => setBrands(data))
      .catch((err) => console.error("Sanity fetch error:", err));
  }, []);

  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 6,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    arrows: true,
    responsive: [
      { breakpoint: 1200, settings: { slidesToShow: 5 } },
      { breakpoint: 992, settings: { slidesToShow: 4 } },
      { breakpoint: 768, settings: { slidesToShow: 3 } },
      { breakpoint: 576, settings: { slidesToShow: 2 } },
    ],
  };

  if (!brands.length) return <p className="text-center">Loading brands...</p>;

  return (
    <div className={styles.Brands}>
      <h3 className={styles.title}>Our Partners</h3>
      <Slider {...settings} className={styles.slider}>
        {brands.map((brand, index) => (
          <div key={index} className={styles.partnerItem}>
            <a href={brand.url} target="_blank" rel="noopener noreferrer">
              {brand.image ? (
                <Image
                  src={urlFor(brand.image)}
                  alt={brand.name || `Brand ${index + 1}`}
                  width={160}
                  height={80}
                  className="img-brand"
                  style={{ objectFit: "contain" }}
                />
              ) : (
                <span>{brand.name}</span>
              )}
            </a>
          </div>
        ))}
      </Slider>
    </div>
  );
};

export default PartnersSection;
